import { makeStyles } from '@material-ui/core';
import { ThemeProvider } from '@material-ui/styles';
import { Home } from './components/templates/Home/Home';
import { LoginUser } from './components/templates/Login';
import theme from './theme/themeConfig';
import CantidadSolicitudes from './services/Solicitudes/CantidadSolicitudes';
import { useContext, useState } from 'react';
import Context from './hooks/UseContext/Solicitudes';
import sonido from './Sounds/sonido.mp3';
import { HomeReportDay } from './components/templates/Home/HomeReportDay';
import {  BrowserRouter as Router, Navigate, Route,Routes} from 'react-router-dom';
import { HomeLeader } from './components/templates/Home/HomeLeader';
import { environment } from './enviroment/enviroment';



function App() {
  const [audio] = useState(new Audio(sonido));
 
  const solicitudes = useContext(Context);
  const [rol, setRol] = useState<any>();
  const [usuario, setUsuario] = useState(localStorage.getItem("user"))
  let id: any;
  async function Cantidad() {
    let cantidadTemp = solicitudes.solicitudes;
    const response: any = await CantidadSolicitudes();
    if (response.status === 200) {
      var xx = response.data.data.Solicitudes;
      var yy: number = +xx;
      solicitudes.solicitudes = yy;
      localStorage.setItem("Solicitudes", solicitudes.solicitudes.toString());
      if (cantidadTemp < solicitudes.solicitudes) {
        audio.play()
      }
    }

  }
 
  return (
    <ThemeProvider theme={theme}>
        <Router>

        <Routes>
          <Route path={environment.LOGIN} element=
            {usuario != undefined ? (

              <Navigate to={environment.HOME} replace={true}></Navigate>

            ) : (
              <LoginUser></LoginUser>
            )}
          />

          <Route path={environment.HOME}  element=
            {usuario != null ? (

              <Home rol={rol} setRol={setRol}></Home>

            ) : (
              <Navigate to={environment.LOGIN} replace={true}></Navigate>
            )}
          />
          <Route path={environment.CALENDAR} element=
            {usuario != null ? (

              <HomeReportDay rol={rol} setRol={setRol} />

            ) : (
              <Navigate to={environment.LOGIN} replace={true}></Navigate>
            )}
          />
          <Route path={environment.REQUESTS} element=
            {usuario != null ? (

              <HomeLeader rol={rol} setRol={setRol} />

            ) : (
              <Navigate to={environment.LOGIN} replace={true}></Navigate>
            )}
          />

          <Route path='*' element={ <> <h1>404 Not found</h1></>}/>

        </Routes>
      </Router>
   
    </ThemeProvider>

  );
}

export default App;
