import { makeStyles } from '@material-ui/core';
import { useState } from 'react';
import cdaLogo from '../../atoms/Logo/cdaLogo.png';
import { InputUsers } from '../../atoms/Inputs/Users/InputUsers';
import { InputPassword } from '../../atoms/Inputs/Password/InputPassword';
import { ButtonPrimary } from '../../atoms/Buttons/Primary/ButtonPrimary';
import Logins from '../../../services/Logins/iniciarSesion';
import { useNavigate } from 'react-router-dom';
import { environment } from '../../../enviroment/enviroment';




const useStyles = makeStyles({
    section1: {
        alignItems: 'center',
        display: ' grid',
        height: ' 100%',
        flexDirection: 'column',
    },
    img: {
        maxWidth: '100%',
        maxHeight: '100vh',
        margin: 'auto',
        marginBottom: ' 26px',
    },
    section2: {
        marginBottom: '1rem',
        color: '#767D7D',
        marginTop: '1%',
        marginLeft: '5%',
        flexDirection: 'column',
    },
    buttons: {
        display: 'flex',
        flexDirection: 'column',
        marginBottom: '2.5rem',
        alignItems: 'center',
        paddingtop: '9rem',
    },
});
interface prop {

    setOpen: (value: boolean) => void;
    setOpenError: (value: boolean) => void;
    setMessage: (value: string) => void;

}

export const FormDetails = ({ setOpen, setOpenError, setMessage }: prop) => {

    // const usu = useContext(useUsuario);
    // const [user] = useState(usu)
    const text = "Iniciar sesin";
    const [usuario, setUsuario] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState(false);
    const [errorUser, setErrorUser] = useState(false);
    const navigate = useNavigate();
    const classes = useStyles();


    const handleClick = async () => {
        try {


            if (password != "" || usuario != "") {
                setError(false);
                setErrorUser(false)
                const response: any = await Logins(usuario, password)

                if (response.data.info.code === "200") {
                    localStorage.setItem("token", response.data.data.access_token);
                    localStorage.setItem("nombre", response.data.data.nombre + ' ' + response.data.data.apellido);
                    localStorage.setItem("apellido", response.data.data.apellido + ' ' + response.data.data.nombre);
                    localStorage.setItem("301", response.data.data.rol);
                    localStorage.setItem("user", usuario);
                    localStorage.setItem("Solicitudes", "0");
                    window.location.replace('');


                    ir();
                } else if (response.data.info.code == '404') {
                    localStorage.setItem("token", "");
                    localStorage.setItem("user", "");
                    setError(true)
                    setErrorUser(true)
                    setOpenError(true)
                    setMessage('Usuario o Contreasea incorrectas')
                }

            } else {
                setError(true);
                setErrorUser(true);
                setOpenError(true)
                setMessage('Campos incompletos')
            }
        } catch (error) {
            setError(true)
            setErrorUser(true)
            setOpenError(true)
            setMessage('Error inesperado, comunquese con el centro de atencin')
        }
    }
    const ir = () => {
        if (localStorage.getItem("user") != "" && localStorage.getItem("token") != "") {
            setOpen(true)
            setMessage('Bienvenido')
            navigate(environment.HOME);

        }
    }


    return (
        <>
            <div>
                <section className={classes.section1} >
                    <img
                        src={cdaLogo}
                        className={classes.img}
                        alt="Logo CDA"
                    />
                </section>


                <form className={classes.section1} >
                    <section className={classes.section2}>
                        <InputUsers usuario={usuario} setUsuario={setUsuario} error={errorUser}></InputUsers>
                    </section>

                    <section className={classes.section2}>
                        <InputPassword password={password} setPassword={setPassword} error={error} />
                    </section>

                    <div className={classes.buttons}>
                        <ButtonPrimary
                            text={text}
                            disabled={false}
                            onClick={handleClick}
                        />
                    </div>


                </form>

            </div>

        </>
    )
}


