import { Card, Grid, makeStyles, Theme } from "@material-ui/core";
import { useEffect, useState } from "react";
import DateRangePicker from "../../atoms/DateRangePicker/DateRangePicker";
import CustomizedSteppers from "../../atoms/Stepper/Stepper";
import { RadioButtonsGroupGeneric } from "../RadioButtonsGroup/RadioButtonsGroupGeneric";

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
    },
    barra: {
        backgroundColor: "#007DC4"
    }
}));
export default function Solicitudes() {

    const classes = useStyles()
    const [value, setValue] = useState('');
    const [dateDesde, setDateDesde] = useState(new Date());
    const [dateHasta, setDateHasta] = useState(new Date());
    const [licencia, setLicencia] = useState(false);
    const [boolRadio,setBoolRadio] = useState(false);
    const [fase,setFase] = useState<number>(0);
    const pasos =['Licencia','Das de licencia']
    
    useEffect(()=>{

        switch(fase){
            case 0:{
                setLicencia(false);
                setBoolRadio(false);
                break;
            }
            case 1:{
                setLicencia(true);
            setBoolRadio(true); 
            break;
            
            }
        }
        // if(value !==''){
        //     setLicencia(true);
        //     setBoolRadio(true);
        //     setFase(1);
        // }
    },[fase])
    return (
        <Grid container>

            <Grid item xs={3}>
                <Card >
                    <Grid item xs={12}>
                        <RadioButtonsGroupGeneric
                            title={'Licencias'} data={[{ id: "1", nombre: 'Vacaciones' }, { id: "2", nombre: 'Da de estudio' }, { id: "3", nombre: 'Enfermedad' }]}
                            value={value}
                            setValue={setValue}
                            disable={boolRadio} />

                    </Grid>
                </Card>
            </Grid>
            {licencia ? (
                <Grid item xs={3} >
                    <Card >
                        <DateRangePicker propDate={dateDesde} setDate={setDateDesde} title={'Desde'}></DateRangePicker>
                        <DateRangePicker propDate={dateHasta} setDate={setDateHasta} title={'Hasta'}></DateRangePicker>
                    </Card>

                </Grid>
            ) : (<></>)
            }


            <Grid item xs={12}>
                <CustomizedSteppers fase={fase} setFase={setFase} steps={pasos}></CustomizedSteppers>
            </Grid>

        </Grid >

    );
};
