import React, { useContext, useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DehazeIcon from '@material-ui/icons/Dehaze';
import HomeIcon from '@material-ui/icons/Home';
import DateRangeIcon from '@material-ui/icons/DateRange';
import ListAltIcon from '@material-ui/icons/ListAlt';
import cdaLogo from '../../atoms/Logo/cdaLogo.png';
import { Badge } from '@material-ui/core';
import Context from '../../../hooks/UseContext/Solicitudes';
import { useNavigate } from 'react-router-dom';
import { environment } from '../../../enviroment/enviroment';

const useStyles = makeStyles({
  list: {
    width: 'auto',
    height: '260%',
    backgroundColor: '#007DC4',

  },
  color: {
    color: "#FFF",
    backgroundColor: '#007DC4',
    '&:hover': {
      backgroundColor: '#F6921E',
    },
    paddingTop: "15px",
    paddingBottom: '15px'
  },
  iconColor: {
    color: '#FFF',
    width: '32px',
    height: '32px'

  },
  iconLine: {

    color: '#007DC4',
    '&:hover': {
      color: '#F6921E',
    },
    width: '32px',
    height: '32px'

  },
  deplegableColor: {
    backgroundColor: '#007DC4',
  },
  section1: {
    width: '100%',
    height: '25%',
    backgroundColor: '#fff'
  },
  img: {
    width: '110px',
    marginLeft: '15%',
    marginTop: '5%',

  },
  bodyDiv: {
    fontFamily: '"Montserrat", sans-serif',
    width: '100%',
    height: '100%',
    backgroundColor: '#F4F4F4'
  },
});

type Anchor = 'left';

interface props {
  rol: string;
  setRol: (value: string) => void;

}

export default function Drawer({ rol, setRol}: props) {
  const classes = useStyles();
  const temp = useContext(Context);
  const [solicitudes] = useState(temp);
  const navigate = useNavigate()
  const [state, setState] = React.useState({
    left: false,
  });
 
  const toggleDrawer = (anchor: Anchor, open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event &&
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  
  

  const list = (anchor: Anchor) => (
    <div
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
      className={classes.deplegableColor}
    > <section className={classes.section1} >
        <img
          src={cdaLogo}
          className={classes.img}
          alt="Logo CDA"
        />
      </section>
      <List className={classes.list}>

        <ListItem button key={"Inicio"} className={classes.color} onClick={() => {
          localStorage.setItem('301', rol);
         
          navigate(environment.HOME);
   
        
        }}>
          <ListItemIcon ><HomeIcon className={classes.iconColor} /> </ListItemIcon>
          <ListItemText  >{"Inicio"}</ListItemText>

        </ListItem >
        <ListItem button key={"Calendario"} className={classes.color} onClick={() => {
  
          navigate(environment.CALENDAR);
         
        }}>
          <ListItemIcon>
            <DateRangeIcon className={classes.iconColor} />
          </ListItemIcon>
          <ListItemText primary={"Calendario"} />
        </ListItem>

        <ListItem button key={"Solicitudes"} className={classes.color} onClick={() => {

            navigate(environment.REQUESTS);
          
            
        }}>
          <ListItemIcon>
            <Badge badgeContent={ solicitudes.solicitudes} color="error"
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}>
              <ListAltIcon className={classes.iconColor} />
            </Badge>
          </ListItemIcon>
          <ListItemText primary={"Solicitudes"} />
        </ListItem>
      </List>

    </div>
  );

  return (
    <div>
      <React.Fragment key={'left'} >
        <Button onClick={toggleDrawer("left", true)}><DehazeIcon className={classes.iconLine}></DehazeIcon></Button>
        <SwipeableDrawer
          anchor={"left"}
          open={state["left"]}
          onClose={toggleDrawer("left", false)}
          onOpen={toggleDrawer("left", true)}
        >
          {list('left')}

        </SwipeableDrawer>
      </React.Fragment>

    </div>
  );
}
