import axios from 'axios'; 
import { environment } from '../../enviroment/enviroment';

const fecha = () => {

    const hoy = new Date();
  
    const fecha = hoy.getFullYear() + '-' +  ('0' + (hoy.getMonth()+1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
  
    return fecha;
  
  };
  

  const idMensagge = () => {
    const hoy = new Date();
  
    const fecha = (hoy.getFullYear()) + ('0' + (hoy.getMonth() + 1)).slice(-2) + ('0' + (hoy.getDate())).slice(-2)+
    ('0' + (hoy.getMinutes())).slice(-2) + ('0' + (hoy.getSeconds())).slice(-2)+('0' + (hoy.getMilliseconds())).slice(-3) + '7';
    return fecha;
  }
export default async function HorasTrabajas(begin:string,end:string,user:string) {

  try {
    const response = await axios({
      url: environment.SERVER_LOCAL+'Schedules',
      method: 'post',
      headers: {
        'Access-Control-Allow-Origin': '*',
      },
      data: {
        header: {
          consulter: localStorage.getItem("user"),
          date: fecha(),
          messageId: idMensagge(),
        },
        data: {
            user: user,
            begin:begin,
            end:end,
          },
        info: {
          message: '',
          code: ''
        }
      }
    }) 
    
    return response;
  }
  catch (e) {
  }
  
}