import {
    makeStyles,
    Theme,
    createStyles,
    withStyles
} from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import { Grid, Paper } from '@material-ui/core';
import { useEffect, useState } from 'react';
import RegistroDeHorasRangos from '../../../services/ConsultaCaliendario/RegistroHoraRangos';
import { formatoFecha } from '../../../function/formatoFecha';

const StyledTableCell = withStyles((theme: Theme) =>
    createStyles({
        head: {
            backgroundColor: '#007DC4',
            fontFamily: '"Montserrat", sans-serif',
            color: theme.palette.common.white,
        },
        body: {
            fontSize: 16,
        },
    }),
)(TableCell);

const StyledTableRow = withStyles((theme: Theme) =>
    createStyles({
        root: {
            '&:nth-of-type(odd)': {
                backgroundColor: "#d8d8d8",
              },
        },
    }),
)(TableRow);
interface prop {
    date: string;
    user: string;
    hours: string;
    minutes: string;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({

        root: {
            fontFamily: '"Montserrat", sans-serif',
            fontWeight: "bold",
            margin: "2px"
        },
        detailsNull: {
            color: '#F6921E',
            fontFamily: '"Montserrat", sans-serif',
            alignItems: 'center',
            backGround: "#fff"

        },
        flecha: {
            background: "#51a7d6",

        },
        titleRow: {
            color: "#fff"
        },
        tableHead:{
            fontSize: 18,

        },
    }),
);

export const AccordionReportDay = ({ date, user, hours, minutes }: prop) => {

    const classes = useStyles();
    const [registroHora, setRegistroHora] = useState<any>(null);
    const [horas, setHoras] = useState("");

    const formatoHoraString = (fecha: any) => {
        if (fecha != null) {
            const d = new Date(fecha);
            return ('0' + (d.getHours())).slice(-2) + ':' + ('0' + (d.getMinutes())).slice(-2) + ' Hrs';
        } else {
            return ('')
        }


    }

    const stringHorasMinutos = (hora: string, minutos: string) => {
        if (horas != null) {
            return (('0' + hora).slice(-2) + ':' + ('0' + minutos).slice(-2) + ' Hrs');
        } else {
            return ('')
        }

    }

    useEffect(() => {
        async function loadDetails() {
            const response: any = await RegistroDeHorasRangos(date, date, user);

            try {

                if (response.status === 200) {
                    setRegistroHora(response.data);
                }
                else {
                    setRegistroHora({});
                }

            } catch (error) {

            }

        }
        loadDetails()
    }, [])

    const getRow = (row: any) => {

        return (

            <StyledTableRow key={row.registroId}>

                <StyledTableCell align='center'>{user}</StyledTableCell>
                <StyledTableCell align='center'>{formatoHoraString(row.begin)}</StyledTableCell>
                <StyledTableCell align="center">{formatoHoraString(row.end)}</StyledTableCell>
                <StyledTableCell align="center">{row.horasRango}</StyledTableCell>
                <StyledTableCell align="center">{row.clienteText}</StyledTableCell>
                <StyledTableCell align="center">{row.lugarTrabajo}</StyledTableCell>
                <StyledTableCell align="center">{row.description}</StyledTableCell>

            </StyledTableRow>

        )

    }
    const filas = () => {

        if (registroHora != null) {

            return (

                registroHora.data.map((row: any) => (

                    getRow(row)

                )
                ));
        }
        return (
            null
        )

    }

    const detalleInicio = () => {


        return (
            <Grid container >
                <Grid item xs={12} sm={12} md={12} lg={12} xl={12} className={classes.root}>
                    <Accordion>
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel1a-content"
                            id="panel1a-header"
                            className={classes.flecha}
                        >
                            <Grid container className={classes.titleRow}>
                                <Typography>
                                    <Grid item xs={12} sm={12} md={12} xl={12}>
                                        {/* <CalendarTodayIcon className={classes.iconRow}></CalendarTodayIcon> {date}{" "} */}
                                        {formatoFecha(date)}{" "}
                                        {/* <AccessTimeIcon className={classes.iconRow}></AccessTimeIcon>  */}
                                        {"Horas totales: "}{stringHorasMinutos(hours, minutes)}
                                    </Grid>
                                </Typography>
                            </Grid>
                        </AccordionSummary>

                        <AccordionDetails className={classes.detailsNull}>
                            <Grid item xs={12}>
                                <TableContainer component={Paper}>
                                    <Table aria-label="customized table">
                                        <TableHead >
                                            <TableRow >
                                                <StyledTableCell align="center" className={classes.tableHead}>Usuario</StyledTableCell>
                                                <StyledTableCell align="center" className={classes.tableHead}>Inicio</StyledTableCell>
                                                <StyledTableCell align="center" className={classes.tableHead}>Fin</StyledTableCell>
                                                <StyledTableCell align="center" className={classes.tableHead}>Horas</StyledTableCell>
                                                <StyledTableCell align="center" className={classes.tableHead}>Cliente/Proyecto</StyledTableCell>
                                                <StyledTableCell align="center" className={classes.tableHead}>Lugar de trabajo</StyledTableCell>
                                                <StyledTableCell align="center" className={classes.tableHead}>Descripcion</StyledTableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {filas()}
                                        </TableBody>
                                    </Table>
                                </TableContainer>
                            </Grid>


                        </AccordionDetails>
                    </Accordion>
                </Grid>

            </Grid>
        )

    }

    return (
        <>
            {detalleInicio()}
        </>        
    )
}


