import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import SettingsIcon from '@material-ui/icons/Settings';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import VideoLabelIcon from '@material-ui/icons/VideoLabel';
import { StepIconProps } from '@material-ui/core/StepIcon';
import { Button, Typography } from '@material-ui/core';
import { AirlineSeatFlatAngledSharp } from '@material-ui/icons';
import { stepClasses } from '@mui/material';


const useColorlibStepIconStyles = makeStyles({
    root: {
        backgroundColor: '#ccc',
        zIndex: 1,
        color: '#fff',
        width: 50,
        height: 50,
        display: 'flex',
        borderRadius: '50%',
        justifyContent: 'center',
        alignItems: 'center',
    },
    active: {
        backgroundImage:
            'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
        boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
    },
    completed: {
        backgroundImage:
            'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
    },
});

interface props {
    fase: number;
    setFase: (value: number) => void;
    steps:string[];
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: '100%',
        },
        button: {
            marginRight: theme.spacing(1),
          },
          instructions: {
            marginTop: theme.spacing(1),
            marginBottom: theme.spacing(1),
          },
    }),
);





export default function CustomizedSteppers({ fase, setFase,steps }: props) {
    const classes = useStyles();
   const pasos = steps;

    function handleBack() {
        setFase(fase - 1);
    }

    function handleNext(){
        setFase(fase+1)
    }
    return (<>
        <div className={classes.root}>
            <Stepper alternativeLabel activeStep={fase}>
                {pasos.map((label) => (
                    <Step key={label}>
                        <StepLabel>{label}</StepLabel>
                    </Step>
                ))}
            </Stepper>

        </div>
        <div>
            <Typography className={classes.instructions}></Typography>
            <div>
                <Button disabled={fase === 0} onClick={handleBack} className={classes.button}>
                    Back
                </Button>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.button}
                >
                    {fase === steps.length - 1 ? 'Finish' : 'Next'}
                </Button>
            </div>
        </div>
    </>
    );
}
