
import React, { useEffect, useState } from 'react';
import { createStyles, makeStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import HorasTrabajadas from '../../../services/HorasTrabajadas/horasTrabajadas';
import { GroupButtonFinished } from '../Buttons/Group/GroupButtonFinished';
import FinalizarDia from '../../../services/FinalizarDia/finalizarDia';
import TableResumen from '../Table/TableResumen';
const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root2: {
            fontFamily: '"Montserrat", sans-serif',
        },
        titulo: {
            backgroundColor: '#007DC4',
            color: '#FFFF',
            fontSize: '100%',
            textAlign: 'center',
            fontFamily: '"Montserrat", sans-serif',
        },

    })
);
const styles = (theme: Theme) =>
    createStyles({
        root: {
            margin: 0,
            padding: theme.spacing(2),

        },
        closeButton: {
            position: 'absolute',
            right: theme.spacing(1),
            top: theme.spacing(1),
            color: theme.palette.grey[500],
        },
    });

export interface DialogTitleProps extends WithStyles<typeof styles> {
    id: string;
    children: React.ReactNode;
    onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const DialogContent = withStyles((theme: Theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(1),
    },
}))(MuiDialogActions);
interface prop {
    abrir: boolean;
    setAbrir: (value: boolean) => void;
    setCambie: (value: boolean) => void;
    stop: () => void;
}

export default function DialogFinished({ abrir, setAbrir, setCambie, stop }: prop) {
    const [open, setOpen] = React.useState(abrir);
    const classes = useStyles();
    const [horas, setHoras] = useState<any>(null);
    const [isLoading, setIsLoading] = useState(true);

    const handleClose = () => {
        setOpen(false);
        setAbrir(false);
        setCambie(true);
    };
    useEffect(() => {
        async function estado() {

            const responseHorasTrabajadas: any = await HorasTrabajadas(new Date().toString());

            if (responseHorasTrabajadas.status === 200) {

                setHoras(responseHorasTrabajadas.data.data);
                setIsLoading(false);

            }
            else {
                setHoras(null);

            }

        }
        estado();
    }, []);
    async function handleClickFinish() {
        const response: any = await FinalizarDia();
        if (response.data.info.code == '200') {
            setOpen(false);
            setAbrir(false);
            setCambie(false);
            stop();
            window.location.replace('');
        } else {

        }
    };

    return (

        <div className={classes.root2}>
            {isLoading ? (
                null
            ) : (
                <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}
                    maxWidth='xl'>
                    <div className={classes.titulo}>
                        <DialogTitle id="customized-dialog-title" onClose={handleClose}  >
                            Resumen
                        </DialogTitle>
                    </div>
                    <DialogContent dividers>
                        <div className={classes.titulo}>
                            <Typography gutterBottom variant='h6'>
                                Horas trabajadas: {horas.hours} Horas y {horas.minutes} Minutos
                            </Typography>
                        </div>


                    </DialogContent>
                    <DialogContent dividers>
                        <TableResumen></TableResumen>
                    </DialogContent>
                    <DialogActions >
                        <GroupButtonFinished
                            cancelDisable={false}
                            finishDisable={false}
                            handleClickCancel={handleClose}
                            handleClickFinish={handleClickFinish}
                        ></GroupButtonFinished>
                    </DialogActions>
                </Dialog>

            )}
        </div>
    );
}