import { useEffect, useState } from 'react';
import { withStyles, Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Alert, Skeleton } from '@material-ui/lab';
import estadoPendientes from '../../../services/CambioInicio/estadoPendientes';
import { ButtonGroupLeader } from '../Buttons/Group/ButtonGroupLeader';
import { Snackbar } from '@material-ui/core';

const StyledTableCell = withStyles((theme: Theme) =>
    createStyles({
        head: {
            backgroundColor: '#007DC4',
            fontFamily: '"Montserrat", sans-serif',
            color: theme.palette.common.white,
        },
        body: {
            fontSize: 14,
        },
    }),
)(TableCell);

const StyledTableRow = withStyles((theme: Theme) =>
    createStyles({
        root: {
            '&:nth-of-type(odd)': {
                backgroundColor: theme.palette.action.hover,
            },
        },
    }),
)(TableRow);


const useStyles = makeStyles({
    table: {
        minWidth: 700,

    },
    button: {
        color: '#fff',
        backgroundColor: '#007DC4',
        '&:hover': {
            backgroundColor: '#F6921E'
        }

    },
});


export default function CustomizedTables() {
    const classes = useStyles();
    const [registroHora, setRegistroHora] = useState<any>(null);
    const [loading, setLoading] = useState(true);
    const [actualizar, setActualizar] = useState(false);
    const [open, setOpen] = useState(false);
    const [openError, setOpenError] = useState(false);
    const [message, setMessage] = useState("")
    const handleClose = () => {
        setOpen(false)
        setOpenError(false)
        setActualizar(!actualizar)
    };
    useEffect(() => {


        async function loadDetails() {
            const response: any = await estadoPendientes()
            try {

                if (response.status === 200) {
                    setRegistroHora(response.data);
                    setLoading(false);
                }
                else {
                    setRegistroHora({});
                }

                
            } catch (error) {

            }

        }
        loadDetails();


    }, [actualizar]);

    const fechas = (fecha: any) => {

        const d = new Date(fecha);



        return ('0' + (d.getHours())).slice(-2) + ':' + ('0' + (d.getMinutes())).slice(-2);
    }

    const anioMesDia = (fecha: any) => {
        const d = new Date(fecha);

        return (d.getFullYear() + '-' + ('0' + (d.getMonth() + 1)).slice(-2) + '-' + ('0' + d.getDate()).slice(-2))
    }

    const filas = () => {

        return (

            registroHora.data.map((row: any) => (
                <StyledTableRow key={row.registroId} >
                    <StyledTableCell align="center">{row.user}</StyledTableCell>
                    <StyledTableCell align='center'>{anioMesDia(row.begin)} </StyledTableCell>
                    <StyledTableCell align="center">{fechas(row.begin) + ' Hrs'}</StyledTableCell>
                    <StyledTableCell align="center">{fechas(row.beginChange)}</StyledTableCell>
                    <StyledTableCell align="center">{row.justification}</StyledTableCell>
                    <StyledTableCell align="center">
                        <ButtonGroupLeader
                            id={row.registroId}
                            setOpen={setOpen}
                            setOpenError={setOpenError}
                            setMessage={setMessage}
                        ></ButtonGroupLeader>
                    </StyledTableCell>

                </StyledTableRow>

            )


            ));

    }
    return (
        <>
            {loading ? (
                <div>

                    <Skeleton variant="text" />
                    <Skeleton variant="rect" />
                    <Skeleton variant="rect" />
                </div>
            ) : (<>
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="customized table">
                        <TableHead>
                            <TableRow>
                                <StyledTableCell align='center'>Usuario</StyledTableCell>
                                <StyledTableCell align="center">Fecha</StyledTableCell>
                                <StyledTableCell align="center">Hora de inicio</StyledTableCell>
                                <StyledTableCell align="center">Hora de inicio a aprobar</StyledTableCell>
                                <StyledTableCell align="center">Justificacin</StyledTableCell>
                                <StyledTableCell align="center">Accin</StyledTableCell>

                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {filas()}
                        </TableBody>
                    </Table>
                </TableContainer>
                <Snackbar open={open} autoHideDuration={2000} onClose={handleClose} >
                    <Alert onClose={handleClose} severity="success">
                        {message}
                    </Alert>
                </Snackbar>
                <Snackbar open={openError} autoHideDuration={2000} onClose={handleClose} >
                    <Alert onClose={handleClose} severity="error">
                        {message}
                    </Alert>
                </Snackbar>
            </>)

            }
        </>
    );

}