import { useContext, useState, useEffect } from 'react';
import { Card, CardHeader, Divider, Grid, makeStyles, Paper, Snackbar, Typography } from '@material-ui/core';
import { RadioButtonsGroup } from '../RadioButtonsGroup/RadioButtonsGroup';
import Skeleton from '@material-ui/lab/Skeleton';
import { ButtonPrimary } from '../../atoms/Buttons/Primary/ButtonPrimary';
import { ValueContext } from '../../../hooks/UseContext/ValueContext';
import HorasTrabajadas from '../../../services/HorasTrabajadas/horasTrabajadas';
import IniciarDia from '../../../services/IniciarDia/iniciarDia';
import FinalizarDia from '../../../services/FinalizarDia/finalizarDia';
import PausarDia from '../../../services/PausarDia/pausarDia';
import Reiniciar from '../../../services/Reiniciar/reiniciar';
import EstadoActual from '../../../services/EstadoActual/estadoActual';
import { RadContext } from '../../../hooks/UseContext/RadContext';
import { Watch } from '../../atoms/Watch/Watch';
import { Alert } from '@material-ui/lab';
import theme from '../../../theme/themeConfig';
import DialogFinished from '../../atoms/Dialog/DialogFinished';
import CambioTarea from '../../../services/CambioTarea/CambioTarea';
import PuedoIniciar from '../../../services/PuedoIniciar/puedoIniciar';
import { monthsToQuarters } from 'date-fns';




const useStyles = makeStyles({
    skeleton: {

        backgroundColor: '#fff',
        marginBottom: '15%',
        marginTop: '7%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-end',
        marginLeft: '5%',
        marginRight: '5%',
        fontFamily: '"Montserrat", sans-serif',

    },
    card: {
        width: '95%',
        marginTop: '5%',
        marginLeft: '3%',
        position: 'relative',
        minWidth: '600px',
        minHeight: '603px',
        height: '650px',
    },
    cardHeader: {
        backgroundColor: '#007DC4',
        color: '#FFFF',
        fontSize: '100%',
        textAlign: 'center',
        fontFamily: '"Montserrat", sans-serif',
    },
    subTitulo: {
        height: '51px',
        backgroundColor: '#F6921E',
        display: 'flex',


    },
    buttons: {
        display: 'flex',
        marginBottom: '1.5%',
        alignItems: 'center',
        fontFamily: '"Montserrat", sans-serif',
        marginLeft: '3%',

    },
    buttonFinish: {
        display: 'flex',
        marginBottom: '1.5rem',
        alignItems: 'center',
        fontFamily: '"Montserrat", sans-serif',
        marginLeft: '11%',
        marginRight: '0.9%',
        minWidth: '350px'
    },
    suTituloText: {
        fontSize: theme.typography.pxToRem(18),
        backgroundColor: '#F6921E',
        color: "#fff",
        fontFamily: '"Montserrat", sans-serif',
        alignItems: 'center',
        display: 'flex',
        margin: 'auto'
    },

    alert: {
        alignItems:"center",
        fontFamily: '"Montserrat", sans-serif',
        textAlign:'center',
        marginLeft:'50px',
        marginRight:'50px'
    }

});
interface props {
    nexo: boolean;
    setNexo: (value: boolean) => void;
    xfecha: string;
    setXfecha: (value: string) => void;
}

export const PaperClock = ({ setNexo, nexo, xfecha, setXfecha }: props) => {

    // Texto de los botones

    const INICIO = "Iniciar actividades";

    const FINALIZAR = "Finalizar actividades";

    const PAUSAR = "Pausar actividades";

    const REANUDAR = "Reanudar actividades";

    const CAMBIO = "Cambio de actividad"

    // Estados del dia

    const INICIADO = "INICIADO";

    const SIN_INICIAR = "SIN_INICIAR";

    const PAUSADO = "PAUSADO";

    const FINALIZADO = "FINALIZADO";

    const classes = useStyles();

    const [visible, setVisible] = useState(false);

    const [text, setText] = useState(INICIO);

    const [valueFinish, setValueFinish] = useState(false);

    const [valuePrimaryButton, setValuePrimaryButton] = useState(false);


    const { valuesRadio } = useContext(ValueContext);
    const [valueLugar, setValueLugar] = useState<string>('');
    const [cambie, setCambie] = useState(false);

    //Prueba de estado de radioButtons

    const { valuesRadioContext } = useContext(RadContext);
    const [disableRadio, setDisableRadio] = useState(valuesRadioContext);

    // API DE ESTADO ACTUAL

    const [estadoActual, setEstadoActual] = useState<any>(null);
    const [isLoading, setLoading] = useState(true);

    //Estructura de Watch

    const [time, setTime] = useState<any>({ ms: 0, s: 0, m: 0, h: 0 });
    const [interv, setInterv] = useState<any>();
    const [horasTrabajadas, setHorasTrabajadas] = useState<any>(null);
    const [cambioTarea, setCambioTarea] = useState(true);
    const [openError, setOpenError] = useState(false);
    const [open, setOpen] = useState(false);
    const [mensaje, setMensaje] = useState('');
    const [popUp, setPopUp] = useState(false);
    const [fechaDeTrabajao, setFechaDeTrabajo] = useState('')
    const [iniciarTrue, setIniciarTrue] = useState(false);

    const fechax = (xfecha: string) => {

 
          const hoy = new Date(xfecha)
          const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
          return fecha;
        
      
      };
      const fechaxd= (xfecha: Date) => {

 
        const hoy = new Date(xfecha)
        const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
        console.log("fechaXD: "+ fecha);
        return new Date(fecha);
      
    };
    const fechaxx= (xfecha: Date) => {

 
        const hoy = new Date(xfecha)
        const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
    
        return fecha
      
    };
    useEffect(() => {
        async function estado() {
            const puedoiniciar: any = await PuedoIniciar()
            if (puedoiniciar.status = 200) {
                setFechaDeTrabajo(new Date(puedoiniciar.data.data.day) == fechaxd(new Date()) ? new Date().toString() : puedoiniciar.data.data.day)
                setIniciarTrue(fechax(puedoiniciar.data.data.day) == fechaxx(new Date()) ? true : false);
                const response: any = await EstadoActual(puedoiniciar.data.data.day);
                const responseHorasTrabajadas: any = await HorasTrabajadas(puedoiniciar.data.data.day);

                if (response.status === 200 && responseHorasTrabajadas.status === 200) {
                    setEstadoActual(response.data.data);
                    setHorasTrabajadas(responseHorasTrabajadas.data.data);
                    setTime({ ms: 0, s: 0, m: responseHorasTrabajadas.data.data.minutes, h: responseHorasTrabajadas.data.data.hours });
                    setLoading(false);
                    setCambie(!cambie);
                    localStorage.removeItem("301");
                }
                else {
                    setEstadoActual(null);

                }
            }

        }
        estado();
    }, [nexo]);



    document.addEventListener("visibilitychange", function () {

        if (visible === true) {
            window.location.replace('/RegistroHorario');
            setVisible(false);

        } else {
            setVisible(true);
        }


    })

    useEffect(() => {
        function changeText() {

            if (isLoading === false) {
                if (estadoActual.estado === SIN_INICIAR) {
                    console.log("Da sin iniciar");
                }
                if (estadoActual.estado === INICIADO) {
                    setText(PAUSAR);
                    setValueLugar(estadoActual.lugarTrabajo)
                    setDisableRadio(valuesRadio);
                    setValuePrimaryButton(!valuePrimaryButton);
                    setValueFinish(!valueFinish);
                    updatedMs = time.ms
                    updatedS = time.s;
                    updatedM = time.m;
                    updatedH = time.h;
                    start();
                }
                else if (estadoActual.estado === PAUSADO) {

                    setText(REANUDAR);
                    setValueFinish(false);
                    setValuePrimaryButton(!valuesRadio);
                }
                else if (estadoActual.estado === "CAMBIO") {
                    setText(REANUDAR);
                    setValueFinish(true);
                    setValuePrimaryButton(false)
                    setText(INICIO)


                } else if (estadoActual.estado === FINALIZADO) {
                    setText(INICIO);
                    setValuePrimaryButton(false);
                    setValueFinish(true);
                    setDisableRadio(true);

                }
            }
        }

        changeText();


    }, [isLoading, cambioTarea]);

    const start = () => {
        setInterv(setInterval(run, 10));
        run();
    };

    var updatedMs = time.ms, updatedS = time.s, updatedM = time.m, updatedH = time.h;

    const run = () => {
        if (updatedM === 60) {
            updatedH++;
            updatedM = 0;
        }
        if (updatedS === 60) {
            updatedM++;
            updatedS = 0;
        }
        if (updatedMs === 100) {
            updatedS++;
            updatedMs = 0;
        }
        updatedMs++;
        return setTime({ ms: updatedMs, s: updatedS, m: updatedM, h: updatedH });
    };

    const stop = () => {
        clearInterval(interv);
    };

    const resume = () => start();

    async function handleClickStart() {
        try {
            if (text === INICIO) {
                const response: any = await IniciarDia(valueLugar);
                if (response.data.info.code == '200') {
                    start();
                    setText(PAUSAR);
                    setDisableRadio(!disableRadio);
                    setValueFinish(!valueFinish);
                    setMensaje('Inicio de actividades exitoso')
                    setOpen(true);
                    setNexo(!nexo)
                    if (localStorage.getItem('Inicio') == 'False') {
                        localStorage.setItem('Inicio', 'True');
                    } else if (localStorage.getItem('Inicio') == 'False') {
                        localStorage.setItem('Inicio', 'True');
                    } else { localStorage.setItem('Inicio', 'True'); }

                } else if (response.data.info.code == '409') {
                    setMensaje('ERROR debe cerrar sus das anteriores para poder iniciar ');
                    setOpenError(true);
                }
            }
            else if (text === PAUSAR) {
                const response: any = await PausarDia('');
                if (response.data.info.code == '200') {
                    stop();
                    setText(REANUDAR);
                    setDisableRadio(!disableRadio);
                    setValuePrimaryButton(!valuePrimaryButton);
                    setValueFinish(!valueFinish);
                    setMensaje('Se Pauso la Actividad');
                    setOpen(true);
                    setNexo(!nexo)

                } else {
                    setMensaje('Error al pausar');
                    setOpenError(true);
                }
            }
            else if (text === REANUDAR) {
                const response: any = await Reiniciar(valueLugar);
                if (response.data.info.code == '200') {
                    resume();
                    setText(PAUSAR);
                    setValueFinish(!valueFinish)
                    setDisableRadio(!disableRadio);
                    setMensaje('Se reanud la actividad');
                    setOpen(true);
                    setNexo(!nexo)
                } else {
                    setMensaje('Error al reanudar la actividad');
                    setOpenError(true);
                }
            }
        } catch (error) {
            setMensaje('ERROR debe cerrar sus das anteriores para poder iniciar ');
            setOpenError(true);
        }
    };
    const handleClose = () => {
        setOpen(false);
        setOpenError(false);
    };

    async function handleClickCHANGE() {
        const response: any = await CambioTarea();
        if (response.data.info.code == '200') {
            setText(INICIO);
            stop();
            setValueFinish(!valueFinish);
            setValuePrimaryButton(!valuePrimaryButton);
            setDisableRadio(!disableRadio);
            setMensaje('Se cambio la actividad');
            setOpen(true);
            setNexo(!nexo)
        } else {
            setMensaje('Error al cambiar la actividad');
            setOpenError(true);
        }
    };
    async function handleClickFinish() {
        const response: any = await FinalizarDia();
        if (response.data.info.code == '200') {
            setText(INICIO);
            stop();
            setValueFinish(!valueFinish);
            setValuePrimaryButton(valuePrimaryButton);
            setDisableRadio(!disableRadio);
            setMensaje('Se finaliz la actividad');
            setOpen(true);
            setNexo(!nexo)
        } else {
            setMensaje('Error al finalizar la actividad');
            setOpenError(true);
        }
    };

    const popUpF = () => {
        if (popUp === true) {
            return (
                <DialogFinished abrir={popUp} setAbrir={setPopUp} setCambie={setCambioTarea} stop={stop}></DialogFinished>
            )
        }
    }

    const buttonsBeginEnd = () => {

        if (isLoading === false) {

            if (estadoActual.estado === SIN_INICIAR) {
                return (
                    <>
                        <Grid container className={classes.buttons}>
                            <Grid item xs={1}>

                            </Grid>
                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={text}
                                    disabled={!valuePrimaryButton}
                                    onClick={() => handleClickStart()}
                                />
                            </Grid>

                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={CAMBIO}
                                    disabled={!valueFinish}
                                    onClick={() => handleClickFinish()}
                                />
                            </Grid>
                            <Grid item xs={1}>

                            </Grid>
                        </Grid>
                        <Divider variant="middle" />
                        <Grid container >

                            <Grid item xs={12} className={classes.buttonFinish}>

                                <ButtonPrimary
                                    text={FINALIZAR}
                                    disabled={!valueFinish}
                                    onClick={() => (setPopUp(true))}
                                />

                            </Grid>

                        </Grid>
                    </>
                )
            }

            if (estadoActual.estado === INICIADO) {
                return (<>
                    <Grid container className={classes.buttons}>
                        <Grid item xs={1}>

                        </Grid>
                        <Grid item xs={5}>
                            <ButtonPrimary
                                text={text}
                                disabled={!valuePrimaryButton}
                                onClick={() => handleClickStart()}
                            ></ButtonPrimary>
                        </Grid>

                        <Grid item xs={5}>
                            <ButtonPrimary
                                text={CAMBIO}
                                disabled={!valueFinish}
                                onClick={() => handleClickCHANGE()}
                            />
                        </Grid>
                        <Grid item xs={1}>

                        </Grid>
                    </Grid>
                    <Divider variant="middle" />
                    <Grid container >
                        <Grid item xs={12} className={classes.buttonFinish}>
                            <ButtonPrimary
                                text={FINALIZAR}
                                disabled={!valueFinish}
                                onClick={() => (setPopUp(true))}
                            />
                        </Grid>
                    </Grid>
                </>
                )
            }

            if (estadoActual.estado === PAUSADO) {

                return (
                    <>
                        <Grid container className={classes.buttons}>
                            <Grid item xs={1}>

                            </Grid>
                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={text}
                                    disabled={!valuePrimaryButton}
                                    onClick={() => handleClickStart()}
                                /></Grid>

                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={CAMBIO}
                                    disabled={!valueFinish}
                                    onClick={() => handleClickCHANGE()}

                                />
                            </Grid>
                            <Grid item xs={1}>

                            </Grid>
                        </Grid>
                        <Divider variant="middle" />
                        <Grid container >
                            <Grid item xs={12} className={classes.buttonFinish}>
                                <ButtonPrimary
                                    text={FINALIZAR}
                                    disabled={!valueFinish}
                                    onClick={() => (setPopUp(true))}
                                />
                            </Grid>
                        </Grid>
                    </>
                )
            }

            if (estadoActual.estado === "CAMBIO") {
                return (
                    <>
                        <Grid container className={classes.buttons}>
                            <Grid item xs={1}>

                            </Grid>
                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={text}
                                    disabled={!valuePrimaryButton}
                                    onClick={() => handleClickStart()}
                                /></Grid>

                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={CAMBIO}
                                    disabled={valueFinish}
                                    onClick={() => handleClickCHANGE()}

                                />
                            </Grid>
                            <Grid item xs={1}>

                            </Grid>
                        </Grid>
                        <Divider variant="middle" />
                        <Grid container >
                            <Grid item xs={12} className={classes.buttonFinish}>
                                <ButtonPrimary
                                    text={FINALIZAR}
                                    disabled={valueFinish}
                                    onClick={() => (setPopUp(true))}

                                />
                            </Grid>
                        </Grid>
                    </>

                )
            }
            if (estadoActual.estado === FINALIZADO) {
                return (
                    <>
                        <Grid container className={classes.buttons}>
                            <Grid item xs={1}>

                            </Grid>
                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={text}
                                    disabled={!valuePrimaryButton}
                                    onClick={() => handleClickStart()}
                                />
                            </Grid>

                            <Grid item xs={5}>
                                <ButtonPrimary
                                    text={CAMBIO}
                                    disabled={valueFinish}
                                    onClick={() => handleClickFinish()}

                                />
                            </Grid>
                            <Grid item xs={1}>

                            </Grid>
                        </Grid>
                        <Divider variant="middle" />
                        <Grid container >
                            <Grid item xs={12} className={classes.buttonFinish}>
                                <ButtonPrimary
                                    text={FINALIZAR}
                                    disabled={valueFinish}
                                    onClick={() => (setPopUp(true))}

                                />
                            </Grid>
                        </Grid>
                    </>

                )
            }
        }
    }
    const muestra = () => {
        if (iniciarTrue == true){
            return (<>
                <RadioButtonsGroup value={valueLugar}
                    setValue={setValueLugar}
                    disableRadio={!disableRadio}
                    cambie={cambie}
                    setCambie={setCambie}
                    SetPrimary={setValuePrimaryButton}
                    valuePrimaryButton={valuePrimaryButton}
                    title={'Lugar de trabajo'} />
                {buttonsBeginEnd()}
            </>
            )
        } else {
            return (
                <Grid container>
                    <Grid item xs={12}>

                        <Alert severity="error" className={classes.alert}>No puede iniciar la actividad de hoy hasta que  no complete este da </Alert>


                    </Grid>
                </Grid>
            )
        }
    }

    const fecha = (date: string) => {

        const hoy = new Date(date);


        return hoy.toLocaleDateString("es-ES", { weekday: 'long', day: 'numeric', month: 'long' });

    };


    return (

        <div >

            {isLoading ? (

                <div className={classes.skeleton}>
                    <Skeleton variant="text" />
                    <Skeleton variant="text" />
                    <Skeleton variant="text" />
                    <Skeleton variant="rect" width={620} height={500} />
                </div>
            ) : (

                <Card className={classes.card}>
                    <Grid container>

                        <Grid item xs={12}>
                            <CardHeader
                                className={classes.cardHeader}
                                disableTypography
                                title={fecha(fechaDeTrabajao)}
                            />
                        </Grid>
                    </Grid>
                    <Grid container>
                        <Grid item xs={12} className={classes.subTitulo}>
                            <Typography className={classes.suTituloText} gutterBottom>
                                Horas transcurridas desde el inicio de actividades
                            </Typography>

                        </Grid>
                    </Grid>
                    <Grid container>
                        <Grid item xs={12}>
                            <Watch time={time} setTime={setTime} loadign={isLoading} />
                        </Grid>
                    </Grid>
                    {/* <Grid container>
                        <Grid item xs={12}>
                            No puede iniciar
                        </Grid>
                    </Grid> */}
                    {/* <RadioButtonsGroup value={valueLugar}
                        setValue={setValueLugar}
                        disableRadio={!disableRadio}
                        cambie={cambie}
                        setCambie={setCambie}
                        SetPrimary={setValuePrimaryButton}
                        valuePrimaryButton={valuePrimaryButton}
                        title={'Lugar de trabajo'} />

                    {buttonsBeginEnd()} */}
                    {muestra()}
                    <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
                        <Alert onClose={handleClose} severity="success">
                            {mensaje}
                        </Alert>
                    </Snackbar>
                    <Snackbar open={openError} autoHideDuration={2000} onClose={handleClose}>
                        <Alert onClose={handleClose} severity="error">
                            {mensaje}
                        </Alert>
                    </Snackbar>

                    {popUpF()}

                </Card>

            )}

        </div>
    )
}
