import axios from 'axios';
import { environment } from '../../enviroment/enviroment';

const fecha = () => {

  const hoy = new Date();

  const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);

  return fecha;

};
const fechas = (d: any) => {

  const hoy = new Date(d);

  const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);

  const hora = hoy.getHours() + ':' + hoy.getMinutes() + ':' + hoy.getSeconds() + '.' + hoy.getMilliseconds();


  return fecha + ' ' + hora;

};

const tipoHora = (tHora: String): number => {

  if (tHora === "Productiva") {
    return (1)
  } else {
    return (2)
  }
}

const idMensagge = () => {
  const hoy = new Date();

  const fecha = (hoy.getFullYear()) + ('0' + (hoy.getMonth() + 1)).slice(-2) + ('0' + (hoy.getDate())).slice(-2) +
    ('0' + (hoy.getMinutes())).slice(-2) + ('0' + (hoy.getSeconds())).slice(-2) + ('0' + (hoy.getMilliseconds())).slice(-3) + '7';
  return fecha;
}

export default async function ActualizarRegistro(inicio: Date, fin: Date, idLugar: number, Descripcion: String,
  idProyecto: number, proyectoText: String, idTipoHora: String, idCliente: number, clienteText: String,
  idRegistro: String) {
  try {
    const response = await axios({
      url: environment.SERVER_LOCAL+'DetailsUpdate',
      method: 'post',
      headers: {
        'Access-Control-Allow-Origin': '*',
      },
      data: {
        header: {
          consulter: localStorage.getItem("user"),
          date: fecha(),
          messageId: idMensagge(),
        },
        data: {
          registroId: idRegistro,
          begin: fechas(inicio),
          end: fechas(fin),
          idLugarTrabajo: idLugar,
          idFase: idProyecto,
          proyectoText: proyectoText,
          idTipoHora: tipoHora(idTipoHora),
          user: localStorage.getItem("user"),
          idCliente: idCliente,
          txt: Descripcion,
        },
        info: {
          message: '',
          code: ''
        }
      }
    })

    return response;
  }
  catch (e) {
  }

}