import axios from 'axios';
import { environment } from '../../enviroment/enviroment';

const idMensagge = () => {
  const hoy = new Date();

  const fecha = (hoy.getFullYear()) + ('0' + (hoy.getMonth() + 1)).slice(-2) + ('0' + (hoy.getDate())).slice(-2) +
    ('0' + (hoy.getMinutes())).slice(-2) + ('0' + (hoy.getSeconds())).slice(-2) + ('0' + (hoy.getMilliseconds())).slice(-3) + '7';
  return fecha;
}

const fecha = (xfecha: string) => {

  if (xfecha == null) {
    const hoy = new Date()
    const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
    return fecha;
  } else {
    const hoy = new Date(xfecha)
    const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
    return fecha;
  }

};

export default async function HorasTrabajadas(date: string) {
  try {
    const response = await axios({
      url: environment.SERVER_LOCAL + 'WorkingHours',
      method: 'post',
      headers: {
        'Access-Control-Allow-Origin': '*',
      },
      data: {
        header: {
          consulter: localStorage.getItem("user"),
          date: new Date().toString(),
          messageId: idMensagge(),
        },
        data: {
          user: localStorage.getItem("user"),
          begin: fecha(date) + " " + "00:00:00",
          end: fecha(date) + " " + "23:59:00"
        },
        info: {
          message: '',
          code: ''
        }
      }
    })


    return response;
  }
  catch (e) {
  }

}