import { withStyles, Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Skeleton } from '@material-ui/lab';
import CalendarTwo from '../Calendar/CalendarTwo';
import { Grid, Typography } from '@material-ui/core';
import Subditos from '../../../services/Subditos/Subditos';
import { SelectSubditos } from '../Select/SelectSubditos';
import { ButtonFindCalendar } from '../Buttons/Primary/ButtonFindCalendar';
import TrendingFlatIcon from '@material-ui/icons/TrendingFlat';
import ClienteProyecto from '../../../services/ClienteProyecto/ClienteProyecto';
import { SelectProyectos } from '../Select/SelectProyectos';
import HorasTrabajas from '../../../services/FiltroHorasTrabajadas/HorasTrabajas';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import HorasTrabajadasXDia from '../../../services/FiltroHorasTrabajadas/HorasTrabajadasXDia';
import { AccordionReportDay } from '../../organisms/Accordion/AccordionReportDay';

const StyledTableCell = withStyles((theme: Theme) =>
    createStyles({
        head: {
            backgroundColor: '#007DC4',
            fontFamily: '"Montserrat", sans-serif',
            color: theme.palette.common.white,
        },
        body: {
            fontSize: 14,
        },
    }),
)(TableCell);

const StyledTableRow = withStyles((theme: Theme) =>
    createStyles({
        root: {

        },
    }),
)(TableRow);


const useStyles = makeStyles({
    date: {
        display: "flex",
        alignItems: "center"
    },
    firstRow: {
        fontWeight: "bold",
        background: "#ffb056"
    },
    info: {
        height: '50%',
        paddingBottom: '3px'
    },
    head: {
        color: '#fff',
        alignItems: 'center',
        textAlign: 'center',
        fontFamily: '"Montserrat", sans-serif',
        fontSize: 26,
    },
    paper: {
        backgroundColor: '#007DC4',
        height: '50px',
    },
    bottonBuscar: {
        padding: '20px',
        float: "right"
    },
    clock: {
        marginLeft: '2%'
    },
  
    horas: {
        alignItems: "left"
    },
});

export default function CustomizedTables() {
    const fecha = (fecha: any) => {
        const d = new Date(fecha);

        return (d.getFullYear() + '-' + ('0' + (d.getMonth() + 1)).slice(-2) + '-' + ('0' + d.getDate()).slice(-2))
    }

    const classes = useStyles();
    const [registroHora, setRegistroHora] = useState<any>(null);
    const [loading, setLoading] = useState(true);
    const [desde, setdesde] = useState(fecha(new Date()));
    const [hasta, setHasta] = useState(fecha(new Date()));
    const [text, setText] = useState<any>(localStorage.getItem("user"));
    const [proyecto, setProyecto] = useState("");
    const [subditos, setSubditos] = useState<any>(null);
    const [proyectos, setProyectos] = useState<any>(null);
    const [consulta, setConsulta] = useState(true);
    const [horas, setHoras] = useState("");
    const [minutos, setMinutos] = useState("");
    const [xdia, setXDia] = useState<any>(null);

    useEffect(() => {
        async function SubditosBox() {
            const response1: any = await Subditos();
            try {

                if (response1.status === 200) {
               
                    setSubditos(response1.data.data)
                    ProyectosBox()
                }
                else {
                    setSubditos({});
                }


            } catch (error) {

            }

        }
        SubditosBox()
    }, []);



    async function ProyectosBox() {
        const response: any = await ClienteProyecto()


        try {

            if (response.status === 200) {
           
                
                setProyectos(response.data.data)
                setLoading(false);

            }
            else {
                setProyectos({});
            }


        } catch (error) {

        }
    }

    async function loadDetails() {

        const response1: any = await HorasTrabajas(desde, hasta, text)
        const response2: any = await HorasTrabajadasXDia(desde, hasta, text)
        console.log("DESDE: " +desde);
        console.log("HASTA: "+hasta);
        try {

            if (response1.status === 200 && response2.status === 200) {

                setHoras(response1.data.data.hours);
                setMinutos(response1.data.data.minutes);
                setXDia(response2.data.data)
                setConsulta(false);
              
            }
            else {
                setRegistroHora({});
                setSubditos({});
            }


        } catch (error) {

        }

    }
    const formatoFecha= (xfecha: string) => {

 
        const hoy = new Date(xfecha)
        console.log("fechaFormato hoy: "+ hoy.getUTCDate())
        const fecha =  (('0' + (hoy.getUTCDate())).slice(-2) + '/'+ ('0' + (hoy.getUTCMonth() + 1)).slice(-2))+'/'+   hoy.getFullYear() ;
    
        return fecha
      
    };
   
    const horastime = (hora: string, minutos: string) => {
        if (horas != null) {
            return (('0' + hora).slice(-2) + ':' + ('0' + minutos).slice(-2) + ' Hrs');
        } else {
            return ('')
        }


    }
   
    return (
        <>
            {loading ? (
                <div>

                    <Skeleton variant="text" />
                    <Skeleton variant="rect" />
                    <Skeleton variant="rect" />
                </div>
            ) : (<>

                <Grid container className={classes.date}>
                    <Grid item xs={12} sm={2} >
                        {/* <SelectProyectos text={"Proyectos"} subText={""} data={proyectos} setText={setProyecto} ></SelectProyectos> */}
                    </Grid>
                    <Grid item xs={12} sm={2}>
                        <SelectSubditos text={"Usuarios"} subText={""} data={subditos} setText={setText} ></SelectSubditos>
                    </Grid>
                    <Grid item xs={12} sm={2} className={classes.clock}>
                        <CalendarTwo setDate={setdesde} title={"Desde"}></CalendarTwo>
                    </Grid>
                    <Grid item xs={12} sm={2} className={classes.clock}>
                        <CalendarTwo setDate={setHasta} title={"Hasta"}></CalendarTwo>
                    </Grid>
                    <Grid item xs={12} sm={3} className={classes.bottonBuscar}>
                        <ButtonFindCalendar text={"Buscar"} disabled={false} onClick={() => loadDetails()}></ButtonFindCalendar>
                    </Grid>

                </Grid>
                <Grid container >
                    <Grid item xs={12} className={classes.info} >
                        <Paper className={classes.paper}>
                            {!consulta ? (<>

                                <Typography className={classes.head}>
                                    <Grid container>
                                        <Grid item xs={1} sm={3} >
                                        </Grid>
                                        <Grid item xs={7} sm={4} >
                                            <CalendarTodayIcon></CalendarTodayIcon> {formatoFecha(desde)} <TrendingFlatIcon></TrendingFlatIcon> {formatoFecha(hasta)}
                                        </Grid>
                                        <Grid item xs={3} sm={2} className={classes.horas}>
                                            <AccessTimeIcon></AccessTimeIcon> {horastime(horas, minutos)}
                                        </Grid>
                                        <Grid item xs={1} sm={2} >
                                        </Grid>
                                    </Grid>
                                </Typography>


                            </>
                            ) : (null)}
                        </Paper>

                    </Grid>
                </Grid>
                <Grid container>
                    {!consulta ? (
                        xdia.map((a: any) =>
                            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                                <AccordionReportDay date={a.date} user={a.usuario} hours={a.hours} minutes={a.minutes}></AccordionReportDay>
                            </Grid>
                        )
                    ) : (null)}
                </Grid>

            </>
            )}
        </>
    );
}