import React, { useContext, useEffect } from 'react';
import { Grid, makeStyles } from '@material-ui/core';
import FormControl from '@material-ui/core/FormControl';
import RadioGroup from '@material-ui/core/RadioGroup';
import Typography from '@material-ui/core/Typography';
import { RadioButtonsActivated } from '../../atoms/RadioButtons/RadioButtonsActivated';
import { ValueContext } from '../../../hooks/UseContext/ValueContext';
import { useState } from 'react';
import LugarTrabajo from '../../../services/LugarTrabajo/lugarTrabajo';
import { Skeleton } from '@material-ui/lab';

interface props {
   title:string;
   data:any;
   value:string;
   setValue:(value:string)=>void;
   disable:boolean;

}

const useStyles = makeStyles({
    container: {
        width: '100%',
        display: 'flex',
        alignItems: 'left'
    },
    text: {
        color: '#B2B2B2',
        fontSize: '100%',
        width: '67%',
        justifyContent: 'flex-start ',
        marginLeft: '9.2%',
        padding: '3%'
    },
    radioGroup: {
        width: '80%',
        justifyContent: 'space-around',
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'end',
        marginTop: '1rem',
        marginBottom: '1.75rem',
        marginLeft: '10%',
        marginRight: '10%',
        color: '#555559'

    },
});


export const RadioButtonsGroupGeneric = ({title,data,value,setValue,disable}:props) => {

    const classes = useStyles();
    const [cargue, setCargue] = useState(false);
   

    useEffect(() => {
        async function lugarTrabajo() {
            // const response: any = await LugarTrabajo()
            if (data!=null){
                setCargue(true);
            }
                    // setData([{ id: "1", nombre: 'YPF/Testing' }, { id: "2", nombre: 'Banco Galicia/Testing' }, { id: "3", nombre: 'Banco Galicia/Producto Nuevo' }])
                 
        }
         lugarTrabajo();   
         
     
    }, [data]);



    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setValue((event.target as HTMLInputElement).value);
    };

    return (
        <div>

            {cargue ? (

                data &&
                <Grid container>
                    <FormControl className={classes.container} component="fieldset">
                        <Typography className={classes.text} variant="body1" >
                            {title}
                        </Typography>
                        <Grid item xs={12}>
                        <RadioGroup
                            className={classes.radioGroup}
                            aria-label={title}
                            name={title}
                            value={value}
                            onChange={handleChange}

                        >{cargue ? (


                            data.map((a: any) =>
                                <Grid item xs={12}>
                                <RadioButtonsActivated
                                    key={a.id}
                                    value={a.nombre}
                                    disabled={disable}

                                />
                                </Grid>
                            )

                        ) : (
                            <Skeleton className={classes.radioGroup} animation="wave" variant='rect' />
                        )}
                        </RadioGroup>
                        </Grid>
                    </FormControl>
                </Grid>
            ) : (

                <div>
                    <Skeleton />
                    <Skeleton animation={false} />
                    <Skeleton animation="wave" />
                    <Skeleton />
                    <Skeleton animation={false} />
                    <Skeleton animation="wave" />
                    <Skeleton />

                </div>
            )}
        </div>
    )
}