
import { AppBarHeader } from '../../organisms/AppBar/AppBarHeader';
import { Body } from '../../organisms/Body/Body';
import { Grid, makeStyles, ThemeProvider } from '@material-ui/core';
import { CalendarDetails } from '../../organisms/CalendarDetails/CalendarDetails';
import { useEffect, useState} from 'react';
import theme from '../../../theme/themeConfig';
import sonido from '../../../Sounds/sonido.mp3';






const useStyles = makeStyles({

    bodyDiv: {
        fontFamily: '"Montserrat", sans-serif',
        width: '100%',
        height: '100%',

    },
});
interface props {
    rol: string;
    setRol: (value: string) => void;
  
  }
export const Home = ({ rol, setRol}: props) => {
    const classes = useStyles();
    const [nexo, setNexo] = useState(false);
    const [xfecha, setXfecha]= useState('');
    const [audio] = useState(new Audio(sonido));
  
    useEffect(()=>{
 
            const r = localStorage.getItem("301") || "comun";
            setRol(r);
  
    
    },[]);

 
   

    
    return (
     
        <ThemeProvider theme={theme}>
        <Grid >
            <Grid container >
                <Grid item xs={12}>
                    <AppBarHeader rol={rol} setRol={setRol}/>
                </Grid>
            </Grid>
            <Grid container className={classes.bodyDiv}>
                <Grid item md={6}>
                    <Body 
                    setNexo={setNexo} 
                    nexo={nexo} 
                    xfecha={xfecha}
                    setXfecha={setXfecha}
                    />
                </Grid>
                <Grid item md={6}>
                    <CalendarDetails 
                    nexo={nexo} 
                    setNexo={setNexo}
                    xfecha={xfecha}
                    setXfecha={setXfecha}
                     />
                </Grid>
            </Grid>

        </Grid>
        </ThemeProvider>
  
    )
}
