import React, { useEffect } from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import NativeSelect from '@material-ui/core/NativeSelect';



const useStyles = makeStyles((theme: Theme) =>
  createStyles({

    container: {
      
      width: '100%',
      display: 'flex',
      alignItems: 'left',
    },
  }),
);
interface prop {
  id: number,
  setId: (value: number) => void,
  setText: (value: string) => void,
  text: String,
  subText: String,
  data: any,
}
export const SelectDetails = ({ id, setId, text, subText, data, setText }: prop) => {
  const classes = useStyles();


  const handleChange = (event: React.ChangeEvent<{ value: string }>) => {
    setId(+event.target.value);
    data.map((item: any) => {
      if (item.id == event.target.value) {
    
        setId(item.id)
        setText(item.nombre);

      }
    });

  };
  useEffect(() => {
    console.log("ENTRE AL CATH")
   try {
    data.map((item: any) => {
      if (item.id == id) {
      
        setText(item.nombre);
        setId(item.id);
      }
    });

   } catch (error) {
  
   }
   
  
    // if (id == 0) {
    //   setText(data[0].nombre);
    //   setId(data[0].id);
    //   console.log("FACE TEXT: " + data[0].nombre)
    //   console.log("FACE TEXT: " + data[0].id)
    // }
    
 
  }, []);



  const opciones = () => {

    return (
      data.map((a: any) =>
        <option
          key={a.id}
          value={a.id}
        >{a.nombre}</option>

      )

    )

  }
  return (
    <div className={classes.container}>
      <FormControl className={classes.container}>
        <InputLabel htmlFor="age-native-helper">{text}</InputLabel>
        <NativeSelect
          value={id}
          onChange={handleChange}
          required={true}
        > <option
        key={0}
        value={0}
      ></option>
          {opciones()}
        </NativeSelect>
        <FormHelperText>{subText}</FormHelperText>
      </FormControl>

    </div>
  );
}
