import axios from 'axios';
import { environment } from '../../enviroment/enviroment';

const idMensagge = () => {
  const hoy = new Date();

  const fecha = (hoy.getFullYear()) + ('0' + (hoy.getMonth() + 1)).slice(-2) + ('0' + (hoy.getDate())).slice(-2) +
    ('0' + (hoy.getMinutes())).slice(-2) + ('0' + (hoy.getSeconds())).slice(-2) + ('0' + (hoy.getMilliseconds())).slice(-3) + '7';
  return fecha;
}

const fecha = () => {

  const hoy = new Date();

  const fecha = hoy.getFullYear() + '-' + (hoy.getMonth() + 1) + '-' + hoy.getDate();

  const hora = hoy.getHours() + ':' + hoy.getMinutes() + ':' + hoy.getSeconds() + '.' + hoy.getMilliseconds();


  return fecha + ' ' + hora;

};

export default async function Reiniciar(valueLugar: string) {

  let idLugar: number = 0;

  if (valueLugar === 'Home Office') {
    idLugar = 1;
  }
  else if (valueLugar === 'Cliente') {
    idLugar = 2;
  }
  else if (valueLugar === 'Presencial CDA') {
    idLugar = 3;
  }

  try {
    const response = await axios({
      url: environment.SERVER_LOCAL + 'Restart',

      method: 'post',
      headers: {
        'Access-Control-Allow-Origin': '*',
      },
      data: {
        header: {
          consulter: localStorage.getItem("user"),
          date: new Date().toString(),
          messageId: idMensagge(),
        },
        data: {
          user: localStorage.getItem("user"),
          hour: fecha(),
          idLugarTrabajo: idLugar,
        },
        info: {
          message: '',
          code: ''
        }
      }
    })


    return response;
  }
  catch (e) {
  }

}