import axios from 'axios';
import { environment } from '../../enviroment/enviroment';


const idMensagge = () => {
  const hoy = new Date();

  const fecha = (hoy.getFullYear()) + ('0' + (hoy.getMonth() + 1)).slice(-2) + ('0' + (hoy.getDate())).slice(-2) +
    ('0' + (hoy.getMinutes())).slice(-2) + ('0' + (hoy.getSeconds())).slice(-2) + ('0' + (hoy.getMilliseconds())).slice(-3) + '7';
  return fecha;
}


const fecha = (xfecha: string) => {

  if(xfecha==null){
    const hoy = new Date()
    const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
    return fecha;
  }else{
    const hoy = new Date(xfecha)
    const fecha = hoy.getFullYear() + '-' + ('0' + (hoy.getMonth() + 1)).slice(-2) + '-' + ('0' + hoy.getDate()).slice(-2);
    return fecha;
  }

};




export default async function EstadoActual(xfecha: string) {


  try {
    const response = await axios({
      url: environment.SERVER_LOCAL+'CurrentState',
      method: 'post',
      headers: {
        'Access-Control-Allow-Origin': '*',
      },
      data: {
        header: {
          consulter: localStorage.getItem("user"),
          date: new Date().toString(),
          messageId: idMensagge(),
        },
        data: {
          user: localStorage.getItem("user"),
          day: fecha(xfecha),

        },
        info: {
          message: '',
          code: ''
        }
      }
    })

    return response;
  }
  catch (e) {
  }

}