import { Badge, Grid, makeStyles } from '@material-ui/core';
import { NameHeader } from '../../atoms/Typography/NameHeader/NameHeader';
import { AvatarProfile } from '../../atoms/Avatar/AvatarProfile';
import { useContext, useEffect, useState } from 'react';
import CantidadSolicitudes from '../../../services/Solicitudes/CantidadSolicitudes';
import Context from '../../../hooks/UseContext/Solicitudes';
import { TreeBar } from '../TreeBar/TreeBar';




const useStyles = makeStyles({
    root: {
        alignItems: 'center',
        display: 'flex',
        justifyContent: 'space-around',
        marginLeft: '0%',
        width: '100%',
        minWidth: '200px'
    },

    section2: {
        alignItems: 'center',
        display: 'flex',
        justifyContent: 'space-between',
        marginLeft: '3.5rem',
        width: '100%',
    },

});

interface props {
    rol: string;
    setRol: (value: string) => void;

}

export const ItemsHeader = ({ rol, setRol }: props) => {
    const classes = useStyles();
    const solicitudes = useContext(Context);
    var userName: string = 'Buenos das ' + localStorage.getItem("nombre");
    var avatarText: string | null = localStorage.getItem("user");

  

    const saludo = () => {

        const hoy = new Date();

        const nombre = localStorage.getItem("nombre");

        if (hoy.getHours() < 12) {

            userName = 'Buenos das ' + nombre
        } else if (hoy.getHours() >= 12 && hoy.getHours() < 20) {
            userName = 'Buenas tardes ' + nombre
        } else {
            userName = 'Buenas noches ' + nombre
        }


        return userName;

    }

    const avatar = () => {
        if (avatarText === null) {
            return (
                ""
            )
        } else {
            return (
                avatarText.substring(0, 2).toUpperCase()
            )
        }
    }


    return (
        <Grid container className={classes.root} >
            <Grid item xs={1} md={7} lg={7} >
                <TreeBar rol={rol} setRol={setRol}></TreeBar>
            </Grid>

            <Grid item xs={5} md={3} lg={2} alignItems={'flex-end'} className={classes.section2}>
                <NameHeader name={saludo()} />
            </Grid>
            <Grid item xs={1} md={3} lg={1} alignItems={'flex-end'} className={classes.section2}>
                <AvatarProfile text={avatar()} rol={rol} setRol={setRol} />
            </Grid>


        </Grid>
    )
}
